#wrapper {
    color: #1c1c1c;
    background-color: #d4edf9;
    background-image: url('https://dev.tofro.hostedby.bristolisopen.com/static/img/tofro-kites.png');
    background-repeat: none;
    background-size: cover;
}
#content {
    max-width:590px;
    font-family:'Arial', sans-serif;
}
.action-column{
    width: 50%;
    display: inline-block;
    font-size: 1rem;
}

.action-column.is-wide {
    display: block;
    width: 100%;
}

.list-heading {
    text-align: center;
    background-color: #009F93;
    padding:10px 0px;
    margin: 0;
    color: white;
}

.list-heading .day{
    font-weight: normal;
}

.action-table {
    width: 100%;
}

.action-table tr {

}

.action-table td {
    padding: 5px 5px;
}

.action-table a {
    text-decoration: none;
    color: inherit;
}

.action-table a:visited {
    color: inherit;
}

a {
    color: #7765E3;
}

a:visited {
    color: #555;
}

button {
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 0.8em;
}

.action {
    border: 3px solid;
    padding: 10px;
    position:relative;
    border-color: #7765E3;
}

.action-title {
    color: #7765E3;
    margin-top: 3px;
    margin-bottom: 3px;
}

a:visited .action-title {
    color: #555;
}

a:visited .action {
    border-color: #555;
}

.action-location {
    font-weight: normal;
}

.action-date {
    font-size: 0.8em;
    font-weight: bold;
}

.action-time {
    float: right;
}

.more-detail{
    float: right;
    font-size: 0.6em;
    margin-top: 3px;
}

.action-summary{
    background-color: #7765E3;
    padding: 0 0 10px 0;
}

.action-summary h2 {
    font-size: 1.6em;
    margin: 10px 0;
    color: white;
}
.pills {
    font-size: 0;
}
.pills > div {
    width: 33%;
    display: inline-block;
}
.sum-pill {
    background-color: white;
    border-radius: 30px;
    width: 70%;
    min-width: 100px;
    padding: 3px 0;
    font-size: 0.8rem;
    margin-left: 2px;
    margin-right: 2px;
}

.sum-number {
    font-size: 1.6rem;
    color: #7765E3;
    font-weight: bold;
}
@media only screen and (min-width: 375px) {
    #wrapper {
        padding: 0px 5px !important;
    }
    #content {
        padding: 20px 0 0px 0 !important;
    }
}
/*Dark mode colors*/
@media (prefers-color-scheme: dark) {
    #wrapper {
      background-color: #666 !important;
      color: white !important;
    }

    #mainHeader{
      background-color: #222 !important;
      color: white !important;
    }

    #mainBody{
      background-color: #222 !important;
      color: white !important;
    }

    #footer {
      background-color: #666 !important;
      color: white !important;
    }

    .list-heading{
      color: #1c1c1c !important;
    }

    .action-summary{
      background-color: #08415C !important;
    }

    .action{
      color: white;
      border-color: #d4edf9 !important;
    }

    .action-title {
      color: #d4edf9 !important;
    }

    a {
      color: #d4edf9 !important;
    }

    a:visited .action-title {
      color: #ccc !important;
    }

    .sum-pill {
      background-color: #037171 !important;
    }

    .sum-number {
      color: inherit !important;
    }
}